body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/*====================BASIC SETUP=================*/
*,
*::after,
*::before {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.App {
    color: white;
    font-size: 14px;
    font-family: 'Nunito Sans', sans-serif;
    background: rgb(32, 44, 54);
    min-height: 100vh;
    padding-bottom: 5rem;
}

/*====================NAV ELEMENT=================*/
nav {
    padding: 2rem 0;
    background: rgb(43, 56, 69);
    box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.47);
    z-index: 20000000;
    position: relative;
}

nav h1 {
    font-size: 1rem;
}

nav div {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    width: 95%;
    margin: auto;

}

@media screen and (min-width: 75rem) {
    nav div {
        max-width: 88rem;
        margin: auto;
    }
}

nav button {
    border: none;
    background: none;
    color: white;
    padding: 0.4rem 0rem;
    border-bottom: solid #fff0 1px;
    transition: all 200ms;
    cursor: pointer;
}

nav button:hover {
    border-bottom: solid rgba(126, 126, 126, 0.55) 1px;
}

/*====================BUTTON STYLING=================*/
.mini-button {
    border: none;
    color: white;
    padding: 0.5rem 1.5rem;
    background-color: rgb(43, 56, 69);
    border-radius: 3px;
    box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.47);
    cursor: pointer;
    transition: all 200ms;
}

.mini-button:hover {
    box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.83);
}

/*====================SEARCH MENUS=================*/
.search-options {
    margin: auto;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    padding: 0 1rem;
}

@media screen and (min-width: 36rem) {
    .search-options {
        -webkit-flex-direction: row;
                flex-direction: row;
        margin-top: 2rem;
        -webkit-justify-content: space-between;
                justify-content: space-between;
    }
}

@media screen and (min-width: 75rem) {
    .search-options {
        max-width: 88rem;
    }
}

.search-box {
    height: 3rem;
    width: 100%;
    max-width: 22rem;
    border: none;
    border-radius: 5px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    font-size: 1.5rem;
    padding-left: 2rem;
    background: rgb(43, 56, 69);
    box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.47);
    transition: all 200ms;
    margin: 1rem 0;
}

@media screen and (min-width: 36rem) {
    .search-box {
        margin: 0;
    }
}

.search-box:hover {
    box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.83);
}

#search-input {
    width: 100%;
    border: none;
    height: 100%;
    margin-left: 1rem;
    background: none;
    color: white;
}

.custom-select {
    background: rgb(43, 56, 69);
    border: none;
    width: 12rem;
    color: white;
    height: 3rem;
    border-radius: 5px;
    position: relative;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    font-size: 0.8rem;
    cursor: pointer;
    box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.47);
    transition: all 200ms;
}

.custom-select:hover {
    box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.83);
}

.custom-options {
    position: absolute;
    bottom: -10.5rem;
    background: rgb(43, 56, 69);
    padding: 1rem 0;
    width: 100%;
    list-style: none;
    border-radius: 5px;
    cursor: pointer;
    box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.47);
    display: none;
}

.custom-options li {
    padding: 0.1rem 2rem;
}

.custom-options li:hover {
    background: #80808082;
}

.revealed {
    display: block;
}

/*====================HOME SECTION LIST OF COUNTRIES=================*/
@media screen and (min-width: 48rem) {
    .country-list {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
    }
}

@media screen and (min-width: 62rem) {
    .country-list {
        grid-template-columns: repeat(3, 1fr);
    }
}

@media screen and (min-width: 75rem) {
    .country-list {
        grid-template-columns: repeat(4, 1fr);
        max-width: 88rem;
        margin: auto;
    }
}

.country-card {
    width: 17rem;
    margin: 1.5rem auto;
    border-radius: 5px;
    overflow: hidden;
    background-color: rgb(43, 56, 69);
    box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.47);
    cursor: pointer;
    transition: all 200ms;
}

.country-card:hover {
    box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.83);
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}

.country-card div {
    height: 12em;
    width: 100%;
    background-size: cover;
    margin-bottom: 2rem;
    background-position: center;
}

.country-card h1 {
    margin-bottom: 1rem;
    margin-left: 1.5rem;
    margin-right: 1rem;
}

.country-card ul {
    list-style: none;
    margin-left: 1.5rem;
    margin-right: 1rem;
    margin-bottom: 2rem;
}

/*====================SECTION COUNTRY DETAILS=================*/
.country-detail-container {
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    padding-top: 7rem;
}

.return-button {
    margin-left: 2rem;
    font-size: 1.1rem;
}

.country-details {
    margin: auto;
    width: 90%;
    margin-top: 4rem;
}

@media screen and (min-width: 62rem) {
    .country-details {
        width: 95%;
        display: -webkit-flex;
        display: flex;
        -webkit-align-items: center;
                align-items: center;
        -webkit-justify-content: space-evenly;
                justify-content: space-evenly;
        max-width: 80rem;
        margin-top: 5rem;
    }
}

.country-details h2 {
    padding-left: 1rem;
    margin-bottom: 1.5rem;
}

.country-details img {
    width: 95%;
    border-radius: 2px;
    margin: auto;
    margin-bottom: 3rem;
    display: inherit;
}

@media screen and (min-width: 23.5rem) {
    .country-details img {
        max-width: 25rem;
    }
}

@media screen and (min-width: 62rem) {
    .country-details img {
        margin: 0rem;
        /* height: 20rem; */
    }
}

.info-container ul {
    list-style: none;
    padding-left: 1rem;
    margin-bottom: 1.5rem;
    max-width: 20rem;
}

@media screen and (min-width: 62rem) {
    .info-container ul {
        padding-left: 0;
        padding-right: 2rem;
    }
}

.country-details div li {
    line-height: 2rem;
}

.border-container {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    margin-top: 1rem;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    width: 100%;
    max-width: 24rem;
}

.border-container button {
    font-size: 0.7rem;
    margin-bottom: 1rem;
    margin-right: 1rem;
}

@media screen and (min-width: 62rem) {
    .info-container {
        display: -webkit-flex;
        display: flex;
    }
}
